body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F5F1DA;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content{
  min-height: calc(100vh - 70px - 64px);
}

.preloader-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 7px auto;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content {
  flex-grow: 1;
}

img,
iframe {
  max-width: 100%;
}

iframe {
  width: 60px;
  height: 35px;
}

.custom-header{
  background-color: #E38851;
}

.custom-footer{
  background-color: #E38851;
}

.watch-button{
  background-color: #A8AC95;
}
.watch-button:hover{
  background-color: #767a60;
}

.common-button{
  background-color: #EAAD7D;
}
.common-button:hover{
  background-color: #ad7142;
}

/* recipe.css */

.recipe {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #fffdf0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.recipe img {
  width: 60%;
  border-radius: 8px;
  margin-bottom: 10px;
  align-self: center;
}

.recipe h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #333;
}

.recipe h6 {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 10px;
}

.recipe p {
  font-size: 1rem;
  line-height: 1.6;
  color: #444;
}

table.centered {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
}

table.centered th, table.centered td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

table.centered th {
  background-color: #e7ceab;
}

table.centered td {
  background-color: #fffef7;
}

.row {
  margin-top: 20px;
}

.row h5 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 20px;
}

.row iframe {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 8px;
}

